body {
    margin: 2px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    padding-left: 10px;
   
    margin-top: 0;
    width: fit-content;
  }
  
  :root{
    --hover-color:rgba(151, 183, 192, 0.776);
    --button-color:rgba(98, 222, 98, 0.733);
    --border-color:rgb(156, 151, 151);
  }
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
  }
  .header{
   margin: 15px;
    /* border: 2px solid red; */
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .header>input{
  
     padding: 10px;
     width: 40vw;
     border-radius: 5px;
     font-size: 20px;
     background-color: rgb(204, 198, 198);
     color:rgb(18, 17, 17);
     border: none;
     outline: none;
  
  }
  h2{
    margin:10px;
  }
  .right-icons{
    padding-right: 20px;
    display: flex;
    flex-direction: row;
    justify-content:space-between;
    gap: 20px;
    
  }
  .right-icons>div{
    padding: 5px 8px;
    border-radius: 50%;
    font-size:25px;
  }
  .right-icons>div:hover{
    background-color: var(--hover-color);
  }
  .drop-down{
    border-right: 1px solid var(--hover-color);
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: fit-content;
    position: absolute;
    height:85vh;
    padding:0;
    padding-right: 0;
    background-color: rgb(206, 204, 153);
  
  }
  .drop-down>div{
      display: flex;
      flex-direction: row;
      gap: 10px;
      /* border: 1px solid blue; */
      font-size: 22px;
      padding: 0.2rem;
      padding-right: 1rem;
      padding-left: 1rem;
      /* margin-left: 5px; */
      border-radius: 7px;
  }
  .drop-down>div:hover{
    background-color: var(--hover-color);
  }
  .create_note{
  /* border: 1px solid rgb(74, 72, 72); */
  border-bottom: none;
    display: flex;
    flex-direction: row;
    /* justify-content: center; */
    align-items: center;
    gap: 15px;
    /* margin-top:10px ; */
    width: fit-content;
    border-radius: 10px;
    
  }
  .create_note>input{
    padding:10px;
    width: 25vw;
    border-radius: 7px;
    font-size: 18px;
    /* background-color: smoky; */
    /* color: rgb(168, 153, 153); */
    border:none;
    outline: none;
    background-color: inherit;
  }
  .create_note>div{
    /* border: 1px solid red; */
    padding: 8px 10px;
    font-size: 25px;
    border-radius: 50%;
    cursor: pointer;
  }
  .create_note>div:hover{
    background-color: var(--hover-color);
    cursor: pointer;
  }
  .textarea{
    /* border: 1px solid red; */
    border-top: none;
    width: 45vw;
    padding: 5px;
    
  }
  @media (min-width: 600px) and (max-width: 900px) {
    .textarea{
      width: 45vw;
    }
  }
  @media (min-width: 100px) and (max-width: 600px) {
    .textarea{
      width: 80vw;
    }
    
  }
  
  .hide{
    display: none;
  }
  .textarea>textarea{
    padding:10px;
    width: 94%;
    border-radius: 7px;
    font-size: 17px;
    /* background-color: smoky; */
    /* color: rgb(168, 153, 153); */
    border:none;
    outline: none;
    margin: 5px;
    background-color: inherit;
  }
  .textarea-icons{
    color: rgb(58, 57, 57);
    display: flex;
    gap: 1.5vw;
    padding-left: 10px;
    flex-wrap: wrap;
  }
  .textarea-icons>div>span{
    font-size: 18px;
    cursor: pointer;
    border-radius: 50%;
    padding: 5px 10px;
    /* padding-bottom: -10px;
    background-color:var(--hover-color); */
  }
  .textarea-icons>div>span:hover{
   background-color:var(--hover-color);
  }
  span>button{
        font-size: 18px;
      border: none;
      background-color: var(--button-color);
      border-radius: 5px;
      outline: none;
      cursor: pointer;
      padding: 5px;
      float: right;
      margin: 5px;
  }
  span>button:hover{
    box-shadow: 2px 2px 5px 2px rgb(45, 46, 46)
  }
  .add-note{
    display:flex;
    flex-direction:column;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;padding-left: 3rem;
   
  }
  .add-div{
    border: 1px solid rgb(58, 57, 57);;
    border-radius: 10px;
    box-shadow: 2px 2px 5px 2px rgb(109, 133, 137);
  }
  
  @media (min-width: 100px) and (max-width: 1070px) {
    .nav{
      display: none;
    }
  }